html {
  scroll-behavior: smooth;
}

body {
  letter-spacing: .03em;
  min-width: 320px;
  cursor: default;
  font-family: DM Sans, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
  list-style: none;
}

img {
  height: auto;
  display: block;
}

@media screen and (max-width: 768px) {
  .footer {
    background-color: #fff5f6;
    flex-direction: column;
    gap: 60px;
    display: flex;
  }

  .footer a {
    color: #d41443;
  }

  .footer__container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;
    display: flex;
  }

  .footer__container-text {
    flex-direction: column;
    display: flex;
  }

  .footer__container-text__title1 {
    letter-spacing: .04em;
    text-transform: uppercase;
    color: #ffa5ba;
    font-family: Titan One;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: 34px;
  }

  .footer__container-text__title2 {
    letter-spacing: .06em;
    text-transform: uppercase;
    color: #d41443;
    font-family: Titan One;
    font-size: 58px;
    font-style: normal;
    font-weight: 400;
    line-height: 66px;
  }

  .footer__container__socials {
    flex-direction: column;
    gap: 40px;
    display: flex;
  }

  .footer__container__socials__container {
    flex-direction: row;
    gap: 30px;
    padding-left: 160px;
    display: flex;
  }

  .footer__container__socials__container-link {
    text-decoration: none;
  }

  .footer__container__socials__container-icon {
    fill: #fea5bb;
    transition: fill .5s cubic-bezier(.4, 0, .2, 1);
  }

  .footer__container__socials__container-icon:hover {
    fill: #d41443;
  }

  .footer__container__socials__contact {
    flex-direction: row;
    display: flex;
  }

  .footer__container__socials__contact__text {
    color: #000;
  }

  .footer__copyright {
    border-top: 1px solid #e1e1e1;
    padding: 10px 15px;
  }

  .footer__copyright__text {
    height: 26px;
    letter-spacing: .02em;
    color: #907e82;
    justify-content: center;
    align-items: center;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 186%;
    display: flex;
  }
}

